import React from 'react';
import style from './FeedbackOptions.module.css';

const FeedbackOptions = ({ options, onLeaveFeedback }) => {
  return (
    <>
      {options.map(option => (
        <button
          key={option}
          className={style.btn}
          type="button"
          onClick={() => onLeaveFeedback(option)}
          name={option}
        >
          {option}
        </button>
      ))}
    </>
  );
};

export default FeedbackOptions;
